<template>
  <div class="section">
    <div class="label-page">
      <div class="label-item" v-for="(v,i) in list" :key="i">
        <img src="@/assets/label-item.png">
        <p class="text">{{v}}</p>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    props: {
      list: {
        default: () => []
      }
    }
  }
</script>

<style scoped lang="less">
  @keyframes topIn {
    from {
      transform: translateY(-50px);
      opacity: 0;
    }
    to {
      transform: translateY(0px);
      opacity: 1;
    }
  }
  .label-page {
    height: 100%;
    padding: 60px 0 100px;
    background: url('~@/assets/mask.png') 0 e('/') cover,
    url('~@/assets/label.png') center e('/') cover;
    display: flex;
    flex-direction: column;
    justify-content: center;
  }
  .label-item {
    position: relative;
    color: #fff;
    font-size: 28px;
    font-weight: bold;
    margin-bottom: 20px;
    opacity: 0;
    img {
      display: block;
      width: 180px;
      margin: auto;
    }
    .text {
      width: 100%;
      height: 100%;
      position: absolute;
      top: 0;
      text-align: center;
      line-height: 52px;
    }
    .active & {
      &:first-of-type {
        animation: topIn 1s ease forwards;
      }
      &:nth-of-type(2) {
        animation: topIn 1s 1s ease forwards;
      }
      &:nth-of-type(3) {
        animation: topIn 1s 2s ease forwards;
      }
      &:nth-of-type(4) {
        animation: topIn 1s 3s ease forwards;
      }
      &:nth-of-type(5) {
        animation: topIn 1s 4s ease forwards;
      }
    }
  }
  .word-cloud {
    height: 280px;
    padding: 30px 10px;
    background: url('~@/assets/mask.png') 0 e('/') cover;
  }
  #cloud {
    height: 100%;
    width: 100%;
  }
</style>