<template>
  <div class="section">
    <div class="content review-sign">
      <div class="text">
        <div class="normal">
          <p><span class="red-bold ml5">{{info.flowSpNum}}</span></p>
          <p><span class="red-bold ml5">{{info.avgTime.hour}}</span></p>
          <p> <span class="orange-bold bigger">{{info.ratio}}</span></p>
<!--          <p v-if="info.startTime"><span class="red"><span class="m5 bold">{{info.startTime.hour}}</span></span></p>-->
        </div>
        <div class="ease">
          <p>
            <span class="red">{{info.latestDate}}</span>
            <span v-if="before"></span>
            <span v-else></span>
          </p>
          <p v-if="info.latestTime">
            <span class="red bold mr5">{{info.latestTime}}</span>
            <span v-if="before"></span>
            <span v-else></span>
          </p>
          <p></p>
          <p><span v-if="before"></span><span v-else></span></p>
        </div>
      </div>
      <div class="tag"><p class="tag-text">{{info.badgeType.value}}</p></div>
      <div class="cloud">
        <img src="@/assets/cloud1.png" class="cloud1">
        <img src="@/assets/cloud2.png" class="cloud2">
        <img src="@/assets/cloud3.png" class="cloud3">
      </div>
      <div class="image">
        <img src="@/assets/sign/img1.png" class="img1">
        <img src="@/assets/sign/img2.png" class="img2">
        <div class="line"></div>
      </div>
      <div class="prompt">
        <p></p>
        <p>APP--</p>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    props: {
      info: {
        default: () => {}
      }
    },
    computed: {
      before() {
        let hour = this.info.latestTime.split(':')[0]
        if (hour < 21 && hour > 5) {
          return true
        } else {
          return false
        }
      }
    },
  }
</script>

<style scoped lang="less">
  .review-sign {
    background: url('~@/assets/sign/bg.png') center e('/') cover;
    .text {
      top: 17.5%;
    }
    //
    @keyframes show1 {
      0% {
        transform: scale(0);
      }
      100% {
        transform: scale(1);
      }
    }
    @keyframes show2 {
      0% {
        width: 0;
        height: 0;
        left: 205px;
        opacity: 1;
      }
      50% {
        width: 170px;
        height: 0;
        left: 35px;
        opacity: 1;
      }
      100% {
        width: 170px;
        height: 65px;
        left: 35px;
        opacity: 1;
      }
    }
    .img1 {
      width: 180px;
      position: absolute;
      right: -55px;
      top: 0;
      transform: scale(0);
      .active & {
        animation: show1 1s forwards;
      }
    }
    .img2 {
      width: 126px;
      position: absolute;
      bottom: 20px;
      left: -35px;
      transform: scale(0);
      .active & {
        animation: show1 1s 2s forwards;
      }
    }
    .line {
      position: absolute;
      top: 90px;
      border-top: 1px solid #ff9494;
      border-left: 1px solid #ff9494;
      opacity: 0;
      .active & {
        animation: show2 1s 1s forwards;
      }
    }
  }
</style>