<template>
  <div class="home">
    <music/>
    <full-page ref="fullpage" :options="options" id="fullpage" v-if="info && isDing">
      <cover/>
      <employee v-if="info.userInfo" :info="info.userInfo"/>
      <review-sign v-if="info.flowInfo" :info="info.flowInfo"/>
      <business-trip v-if="info.evectionInfo" :info="info.evectionInfo"/>
      <office-data v-if="info.officeInfo" :info="info.officeInfo"/>
      <seal v-if="info.forensicProcessingInfo" :info="info.forensicProcessingInfo"/>
      <post-it v-if="info.informationInfo" :info="info.informationInfo"/>
      <settlement v-if="info.settlementInfo" :info="info.settlementInfo"/>
      <EFT v-if="info.capitalAllocationInfo" :info="info.capitalAllocationInfo"/>
      <contract v-if="info.allContractInfo" :info="info.allContractInfo"/>
      <sales-data v-if="info.saleAmountInfo" :info="info.saleAmountInfo"/>
      <invoice-check v-if="info.financeInvoiceInfo" :info="info.financeInvoiceInfo"/>
      <thanks/>
    </full-page>
    <div v-if="!isDing" class="not-ding"></div>
  </div>
</template>

<script>
  import {
    Cover,
    PostIt,
    Contract,
    BusinessTrip,
    OfficeData,
    Employee,
    ReviewSign,
    Thanks,
    Seal,
    InvoiceCheck,
    EFT,
    SalesData,
    Settlement,
    Music
  } from '@/components'
  import {corpId} from "@/env"

  export default {
    name: 'home',
    components: {
      Cover,
      PostIt,
      Contract,
      BusinessTrip,
      OfficeData,
      Employee,
      ReviewSign,
      Thanks,
      Seal,
      InvoiceCheck,
      EFT,
      SalesData,
      Settlement,
      Music
    },
    data() {
      return {
        options: {
          licenseKey: 'OPEN-SOURCE-GPLV3-LICENSE',
          menu: '#menu',
          afterLoad: this.afterLoad,
        },
        index: 0,
        info: '',
        isDing: true
      }
    },
    created() {
      const _this = this
      this.$nextTick(() => {
          if (dd.env.platform == "notInDingTalk") {
            this.isDing = false
            return
          } else {
            this.isDing = true
            dd.ready(function () {
              dd.runtime.permission.requestAuthCode({
                corpId: corpId, // id
                onSuccess: function (info) {
                  // console.log(info)
                  let code = info.code // 
                  _this.getData(code)
                }
              })
            })
          }
        }
      )
    },
    methods: {
      componentsReady() {
        this.$refs.fullpage.init()
      },
      afterLoad(anchorLink, index) {
        // console.log(anchorLink, index)
        this.index = index.index
      },
      getData(code) {
        this.$axios.get('/report/getData', {params: {oaAccount: code}}).then(res => {
          this.info = res.data
          // console.log(res.data)
        })
      }
    }
  }
</script>
