<template>
  <!---->
  <div id="bgm">
    <audio  id="bg-music" autoplay preload="auto" loop="loop">
      <source src="@/assets/bgm.mp3" type="audio/mp3" />
    </audio>
    <img :src="playFlag ? playImg : pauseImg" :class="{rotate: playFlag}" @click="bgMusicPlayOrPause('bg-music')">
  </div>
</template>

<script>
  export default {
    data() {
      return {
        playFlag: true,
        playImg: require('@/assets/play.png'),
        pauseImg: require('@/assets/pause.png'),
        clickMusicBtn: false
      }
    },
    mounted() {
      this.audioAutoPlay('bg-music');

      document.addEventListener("visibilitychange", (e) => { // iosQ
        if (this.clickMusicBtn) { // 
          if (this.playFlag) {
            this.audioAutoPlay('bgMusic');
            this.playFlag = true;
          } else {
            this.audioPause('bgMusic');
            this.playFlag = false;
          }

          text.innerHTML = e.hidden;
          if (e.hidden) {  // 
            this.audioAutoPlay('bg-music');
            this.playFlag = true;
          } else { // 
            this.audioPause('bg-music');
            this.playFlag = false;
          }
        } else { // 
          if (this.playFlag) {
            this.audioPause('bg-music');
            this.playFlag = false;
          } else {
            this.audioAutoPlay('bg-music');
            this.playFlag = true;
          }

          text.innerHTML = e.hidden;
          if (e.hidden) {  // 
            this.audioPause('bg-music');
            this.playFlag = false;
          } else { // 
            this.audioAutoPlay('bg-music');
            this.playFlag = true;
          }
        }
      });
    },
    methods: {
      bgMusicPlayOrPause(id) {
        this.clickMusicBtn = !this.clickMusicBtn;
        if (this.playFlag) {
          this.audioPause(id);
          this.playFlag = false;
        } else {
          this.audioAutoPlay(id);
          this.playFlag = true;
        }
      },
      audioPause(id) {
        console.log('pause')
        let audio = document.getElementById(id);
        audio.pause();
        document.addEventListener("WeixinJSBridgeReady", function () {
          audio.pause();
        }, false);
        document.addEventListener('YixinJSBridgeReady', function () {
          audio.pause();
        }, false);
      },
      audioAutoPlay(id) {
        console.log('play')
        let audio = document.getElementById(id);
        audio.play();
        document.addEventListener("WeixinJSBridgeReady", function () {
          audio.play();
        }, false);
        document.addEventListener('YixinJSBridgeReady', function () {
          audio.play();
        }, false);
      }
    }
  }
</script>

<style scoped lang="less">
  #bgm {
    position: absolute;
    right: 10px;
    top: 10px;
    z-index: 1000;
    opacity: 0.6;
    img {
      width: 25px;
      display: block;
    }
  }
  @keyframes rotate{
    0%{
      transform: rotate(0);
    }
    50%{
      transform:rotate(180deg);
    }
    100%{
      transform: rotate(360deg);
    }
  }
  .rotate {
    transition: 0.5s;
    animation: rotate 3s linear infinite;
  }
</style>